.catalog-container__title {
  font-family: $title-font;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: $black;
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.catalog__item {
  margin-bottom: 19px;
}

.catalog__item-title {
  font-family: $title-font;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: rgba(34, 34, 34, 1);
  text-transform: uppercase;
}

.catalog__description-list {
  font-size: 12px;
  line-height: 16px;
  color: rgba(68, 68, 68, 1);
}

.catalog__circumscribing-first {
  font-family: $title-font;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: rgba(17, 17, 17, 1);
  text-transform: uppercase;
}

.catalog__circumscribing-second {
  font-size: 12px;
  line-height: 16px;
  color: rgba(68, 68, 68, 1);
}

button.catalog__button-secondary {
  font-size: 16px;
  line-height: 20px;
  color: rgba(68, 68, 68, 1);
  background-color: rgba(242, 242, 242, 1);
}

button.catalog__button,
button.additional-goods__button {
  font-size: 16px;
  line-height: 20px;
}
